import React, { useState, useCallback } from 'react';
import CandlestickChart from './components/candlestickChart';

const App: React.FC = () => {
    const [timeframe, setTimeframe] = useState<string>('1h'); 

    //      
    const handleSetTimeframe1h = useCallback(() => setTimeframe('1h'), []);
    const handleSetTimeframe5m = useCallback(() => setTimeframe('5m'), []);

    return (
        <div className="App">
            <h1>  BTC/USDT</h1>
            <div>
                {/*       */}
                <button onClick={handleSetTimeframe1h}>1 </button>
                <button onClick={handleSetTimeframe5m}>5 </button>
            </div>
            {/*         */}
            <CandlestickChart symbol="BTCUSDT" interval={timeframe} />
        </div>
    );
};

export default App;
