import React, { useEffect, useState } from 'react';
import ReactApexChart from 'react-apexcharts';
import { getCandlestickData } from '../services/binanceApi';

interface CandlestickChartProps {
    symbol: string; //   , , "BTCUSDT"
    interval: string; //  , , "1h"  "5m"
}

const CandlestickChart: React.FC<CandlestickChartProps> = ({ symbol, interval }) => {
    const [series, setSeries] = useState<any[]>([]); //    
    const [loading, setLoading] = useState<boolean>(true); //  

    useEffect(() => {
        //         
        const fetchData = async () => {
            setLoading(true); //   
            try {
                //        getCandlestickData
                const data = await getCandlestickData(symbol, interval);
                // ,      
                if (data && Array.isArray(data)) {
                    //      ApexCharts
                    const formattedData = data.map((d: any) => ({
                        x: new Date(d[0]), //   
                        y: [parseFloat(d[1]), parseFloat(d[2]), parseFloat(d[3]), parseFloat(d[4])] // [Open, High, Low, Close]
                    }));
                    //      
                    setSeries([{ data: formattedData }]);
                } else {
                    setSeries([]); //   ,   
                }
            } catch (error) {
                //     
                console.error('   :', error);
                setSeries([]); //     
            } finally {
                setLoading(false); //   
            }
        };

        fetchData(); //       symbol  interval
    }, [symbol, interval]);

    //   ApexCharts
    const options = {
        chart: {
            type: 'candlestick' as const, //     "candlestick"
            height: 350
        },
        title: {
            text: `${symbol} - ${interval}`, //  
            align: 'left' as const 
        },
        xaxis: {
            type: 'datetime' as const //    X
        },
        yaxis: {
            tooltip: {
                enabled: true //      Y
            }
        }
    };

    //   ,   
    if (loading) {
        return <div> ...</div>;
    }

    return (
        <div id="chart">
            {/* ,      */}
            {series.length > 0 ? (
                <ReactApexChart options={options} series={series} type="candlestick" height={350} />
            ) : (
                <div>   </div>
            )}
        </div>
    );
};

export default CandlestickChart;
