import axios from 'axios';

//  URL  API Binance
const BASE_URL = 'https://api.binance.com';

//           
export const getCandlestickData = async (symbol: string, interval: string) => {
    try {
        //  GET-  Binance API    ()  
        const response = await axios.get(`${BASE_URL}/api/v3/klines`, {
            params: {
                symbol,
                interval,
                limit: 100 //     100 
            }
        });
        return response.data;
    } catch (error) {
        //     
        console.error('     :', error);
        throw error;
    }
};
